import React from 'react';
import styled from 'styled-components'

export default function ResultBlock({price, oldPrice, square}) {

    return (
        <Block>
            <SmallText>{`  : ${square} 2`}</SmallText>
            <p>{`  : ${price} `}</p>
            <SmallText>{' (, )    '}</SmallText>
            <SmallText>{'     '}</SmallText>
        </Block>
    )
}

const Block = styled.div`
    padding: 10px;
`;
const SmallText = styled.p`
    font-size: 14px;
`;