import React from 'react';
import styled from 'styled-components'

import {H2} from '../../common/Atom';

export default function PresentationText({}) {
    return (
        <TextWrapper>
            <H2>{'-  '}</H2>
            <H2>{'-    '}</H2>
            <H2>{'-  '}</H2>
            <H2>{'- c    '}</H2>
            <H2>{'-   '}</H2>
            <H2>{'-     '}</H2>
        </TextWrapper>
    )
};

const TextWrapper = styled.div`
    ${H2} {
        text-align: left;
    } 
`;